import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import { toast } from "react-toastify";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import standardOutputApi from "api/common/standardOutput";
import majorStandardOutputApi from "api/common/majorStandardOutput";
import SubMajorApi from "api/common/subMajor";
import { useHistory } from "react-router-dom";

import {
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  ButtonGroup 
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";


const MajorStandardOutput = ()=> {
  const { t } = useTranslation();
  const id = queryString.parse(window.location.search);
  const [isLoading, setIsLoading] = useState(false);
  const [listSubMajor, setListSubMajor] = useState([]);
  const history = useHistory();
  useEffect(async () => {
    setIsLoading(true)
    try{
      const res = await SubMajorApi.getAll();
      if (res) {
        setListSubMajor(res.contents);
      }
    }catch(err){
      setListSubMajor([]);
      if(err.response){
        if (
            err.response.status === 403 ||
            err.response.status === 401
        ) {
            toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err.response.status === 400) {
              toast.error(t("RESPONSE_MESSAGE400"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }  
    }
    setIsLoading(false);
  },[])
  return (<>
    {isLoading && <LoadingBar />}
      <Breadcrumbs
        currentItem={
          t("admin.major_standard_output_edit.LIST")
        }
        items={[
          {
            href: RoutesString.ADMIN_STANDARD_OUTPUT_LIST,
            title: t("admin.standard_output_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={
          t("admin.major_standard_output_edit.LIST")
        }
      />

      <div className="page-content">

      <ButtonGroup vertical>
        {listSubMajor?.map((item, index) => {
          return (
            <Button key={index + 1} onClick={()=>history.push(RoutesString.ADMIN_MAJOR_STANDARD_OUTPUT_EDIT.replace(":id", item.id))}>
              
            </Button>
          )})};
      </ButtonGroup>
      </div>
  </>
  )
};

export default MajorStandardOutput;